.register {
  position: absolute;
  bottom: 0;
  width: 100%;
  left: 0;
  top: 0;
  overflow: hidden;

  position: absolute;
	bottom: 0;
	width: 100%;
	left: 0;
	top: 0;
	overflow: hidden;
  .mint-cell-wrapper{
    background-size: 120% 0px;
  }
	.logo {
		background-image: url("../../images/logo.png");
		width: 128px;
		height: 41px;
		margin: 56px auto 96px;
	}

	.ipt_box {
		width: 90%;
		margin: 0 auto 20px;
		position: relative;
	}

	.ipt_username {
		width: 100%;
		border: 0;
		border-bottom: 1px solid #cccccc;
		outline: none;
		font-size: 14px;
		line-height: 30px;
	}

	.ipt_phonecode {
		width: 100%;
		border: 0;
		border-bottom: 1px solid #cccccc;
		outline: none;
		font-size: 14px;
		line-height: 30px;
	}

	.ipt_sendCode {
		position: absolute;
		right: 2px;
		top: 0;
		height: 30px;
		line-height: 30px;
		color: #333333;
		cursor: pointer;
		font-size: 14px;
	}

	.btn_box {
		width: 90%;
		margin: 0 auto 20px;
		position: relative;
	}

	.btn_login_pay_over {
		width: 327px;
		height: 42px;
		border-radius: 3px;
		border: 0;
		background-color: #6eb41e;
		color: #ffffff;
		font-size: 16px;
		width: 100%;
		outline: none;
		cursor: pointer;
	}

	.btn_login_over {
		width: 327px;
		height: 42px;
		border-radius: 3px;
		border: 0;
		background-color: #f0851b;
		color: #ffffff;
		font-size: 16px;
		width: 100%;
		outline: none;
		cursor: pointer;
	}

	.btn_login_out {
		width: 327px;
		height: 42px;
		border-radius: 3px;
		border: 0;
		background-color: #cccccc;
		color: #ffffff;
		font-size: 16px;
		width: 100%;
		outline: none;
		cursor: pointer;
	}

	.tipsbox {
		font-size: 12px;
		width: 90%;
		margin: 0 auto;
		color: #9b9b9b;
	}

	.tipsbox .ico {
		float: left;
	}

	.tipsbox .content {
		float: left;
		margin-left: 3px;
	}

	.fa-circle-o:before {
		font-size: 20px;
	}

	.fa-check-circleover:before {
		font-size: 20px;
		color: #ff8903;
	}

	.fa-exclamation-circle:before {
		font-size: 30px;
		color: #ffffff;
	}
  .btn_disable {
      color: #ccc !important;
  }
}



