
.page-loadmore-desc{text-align:center;color:#666;padding-bottom:5px
}
.page-loadmore-desc:last-of-type{border-bottom:solid 1px #eee
}
.page-loadmore-listitem{display:block;height:50px;line-height:50px;border-bottom:solid 1px #eee;text-align:center
}
.page-loadmore-listitem:first-child{border-top:solid 1px #eee
}
.page-loadmore-wrapper{z-index:10;overflow:scroll
}
.mint-loadmore-bottom span{display:inline-block;-webkit-transition:0.2s linear;transition:0.2s linear;vertical-align:middle
}
.mint-loadmore-bottom span .is-rotate{-webkit-transform:rotate(180deg);transform:rotate(180deg)
}
.bottom-arrow-hide{display:none
}



