
@import url(assets/css.css);
@import url(assets/icon.css);
#app{font-family:"Segoe UI","Lucida Grande",Helvetica,Arial,"Microsoft YaHei",FreeSans,Arimo,"Droid Sans","wenquanyi micro hei","Hiragino Sans GB","Hiragino Sans GB W3",FontAwesome,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#2c3e50
}
.views{width:100%;height:100%;-webkit-transition:all .2s ease;transition:all .2s ease;position:absolute;top:0;left:0
}
.container{width:1000px;margin:0 auto
}
.left{float:left;text-align:left
}
.right{float:right
}
.md-toolbar .title{vertical-align:text-top;margin-left:5px
}
h1,h2{font-weight:normal
}
ul{list-style-type:none;padding:0
}
li{display:inline-block
}



